.card-contribute{background:#444;}.card-contribute a{margin:auto;font-size:18px;line-height:22px;padding:31px 20px 71px;}.card-contribute a:after{position:absolute;left:50%;-webkit-transform:translateX(-50%);-ms-transform:translateX(-50%);transform:translateX(-50%);width:40px;height:40px;padding:10px 0;font-size:40px;line-height:15px;bottom:50%;margin-bottom:-50px;background:rgba(255,255,255,0.5);border-radius:50%;content:'+';color:white;text-align:center;text-shadow:none;-webkit-transition:all .2s ease-in;-o-transition:all .2s ease-in;transition:all .2s ease-in}.card-contribute:hover a:after{background:#40bae2}@media (min-width:401px){.card-contribute a{font-size:22px;line-height:28px;padding:50px 20px 110px;}.card-contribute a:after{width:50px;height:50px;padding:13px 0;margin-bottom:-60px;font-size:50px}}


