.header-header,.header-nav{width:100%;height:50px;position:fixed;top:0;left:0}.header-header{z-index:5;background:black;-webkit-box-shadow:0 2px 3px rgba(0,0,0,0.2);box-shadow:0 2px 3px rgba(0,0,0,0.2);}.header-header .header-nav{padding:10px 15px;background:#40bae2;-webkit-animation:fadein .4s;animation:fadein .4s}.header-header h1{margin-top:8px;margin-left:-8px;float:left;}.header-header h1 a{color:white;padding:5px 8px;}.header-header h1 a.nav-about{margin-left:1px;color:#444}.header-header h1 a:hover{opacity:.5}.header-header input{width:50%;margin-top:1px;padding:4px 30px;float:right;background:white url("images/icon-search.svg") no-repeat 8px 6px;background-size:14px 14px;border-radius:4px;border:1px solid white;font-size:16px;font-family:Georgia,serif;font-style:italic;line-height:1;}.header-header input::-webkit-input-placeholder{padding-top:2px;font-size:14px}.header-header input::-moz-placeholder{font-size:14px;color:#666}.header-header input:focus{outline:none}.header-header .clear{width:13px;height:13px;position:absolute;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);right:22px;background:url("images/icon-close-grey-red.svg") no-repeat 0 0;background-size:13px 26px;border:none;cursor:pointer;}.header-header .clear:hover{background-position:0 -13px}.header-header .clear:focus{outline:none}@media (max-width:500px){.header-header .clear{display:none}.header-header input{width:30px;margin-left:15px;padding-right:0;position:absolute;right:15px;-webkit-transition:width ease-out .2s;-o-transition:width ease-out .2s;transition:width ease-out .2s;}.header-header input:hover{cursor:pointer}.header-header input:focus{width:calc(100% - 30px);padding-right:30px;}.header-header input:focus:hover{cursor:default}.header-header input:focus + .clear{display:block}}


