.site-info{width:100vw;height:100vh;position:fixed;top:0;left:0;z-index:10;background:black no-repeat 50% 50%;background-size:cover;text-align:center;color:white;-webkit-animation:fadein .4s;animation:fadein .4s;-webkit-transition:opacity .2s ease-out;-o-transition:opacity .2s ease-out;transition:opacity .2s ease-out;}.site-info.hidden{visibility:hidden;opacity:0;-webkit-transition-delay:0s;-o-transition-delay:0s;transition-delay:0s}.site-info .back{z-index:30}.site-info .container{width:100%;height:100%;padding:30px 15px;position:relative;z-index:2;overflow-y:auto}.site-info .content{max-width:550px;margin:80px auto 50px;}@media (max-width:500px){.site-info .content{margin-top:50px}}.site-info h1{margin-bottom:20px;font-size:40px}.site-info p,.site-info ul{margin-bottom:15px;text-align:left;}.site-info p a,.site-info ul a{border-bottom:1px dotted transparent;color:#40bae2;font-weight:700;}.site-info p a:hover,.site-info ul a:hover{border-color:#40bae2}.site-info .quote{font-size:22px;text-align:center}.site-info ul{padding-left:30px;font-family:"clavo",Georgia,serif;font-style:italic;font-size:18px;line-height:1.4;list-style-type:disc;}.site-info ul li{margin-bottom:15px}.site-info hr{margin:30px 0;border:none;border-bottom:1px dotted rgba(255,255,255,0.3)}.site-info .stats{margin-top:30px;text-align:center;font-size:20px;}.site-info .stats p{margin-bottom:0;display:inline-block;text-align:center}.site-info .stats span{padding:0 5px;display:inline-block;vertical-align:middle}.site-info .stats .stat{font-family:"brandon-grotesque",Helvetica,Arial,sans-serif;font-weight:900;font-style:normal;font-size:50px}.site-info .footnote{margin-top:40px;}.site-info .footnote p{margin-bottom:10px;font-size:15px;}.site-info .footnote p a{color:white}.about{background-image:url("images/airplane.gif")}.btn{margin:15px 0;padding:15px 20px 12px;display:inline-block;background-color:#40bae2;border-radius:3px;font-weight:700;text-transform:uppercase;color:white;}.btn:hover{background-color:#1981a4}.btn.btn-secondary{margin:0;padding:7px 10px 8px;background-color:rgba(64,186,226,0.4);font-size:14px;}.btn.btn-secondary:hover{background-color:#40bae2}


